<template>
  <div class="twocloumn" v-html="content"></div>
</template>

<script>
  import mixinComponent from 'core/mixin'
  var DEFAULT_CONTENT = `
	<div class="plekan-clearfix">
		<h2 contenteditable="true" class="twocloumn-title">Schiaparelli kayp</h2>
      <div contenteditable="true" class="twocloumn-text">ESA, Schiaparellinin Marsa nceki gnk inii srasnda beklenenden farkl hareket ettiini aklad. Kurumun gezegenlerle ilgili almalarndan sorumlu Andrea Accomazzo, robota ne olduunu bilemediklerini itiraf etti.

        <p>Keif robotunu frlatan uydudan gelen veriler, robotun, paratn ok erken atn gsteriyor.</p>

        Ayrca robotun yzeye iyice yaklatnda kendisini yere oturtmaya yarayacak roketleri de beklenenden ok daha ksa sre altrd tahmin ediliyor.

        BBC Trkedeki habere gre ESA henz robotun inerken paralanp paralanmadn kesin olarak belirleyemedi ama iyimser de deil. Uzmanlar, verileri incelemeye ve kk bir ihtimal de olsa gezegen yzeyine oturduysa diye Schiaparelliye sinyaller yollamaya devam ediyor. u anda ESAnn btn yapabilecei, Schiaparellinin inie 1 dakika kalana kadar, kendisini frlatan TGO adl uyduya gnderdii verileri taramak suretiyle anlayabilmek.
      </div>
	</div>
  `
  export default {
  	mixins:[ mixinComponent ],
    data () {
      return {
        DEFAULT_CONTENT : DEFAULT_CONTENT
      }
    },
    updated(){
      // this.me.contents[this.displayLanguage].fields["src"] = this.$el.querySelector('img').src
    }
  }
</script>
<style>
  .twocloumn-title{
    padding-left: 30px;
  }
  .twocloumn-text{
    margin-bottom: 29px;
    padding-left: 30px;
  }
</style>


